<template>
  <div class="result-container flex-column flex-1" v-loading="loading">
    <table class="pure-table">
      <thead>
        <tr>
          <th>Username</th>
          <th>Title</th>
          <th>Score</th>
          <th>Total Score</th>
          <th>Time Used</th>
          <th>Time Limit</th>
          <th>Test At</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>
            {{ result.username }}
          </td>
          <td>
            {{ result.title }}
          </td>
          <td>
            {{ result.score }}
          </td>
          <td>
            {{ result.totalScore }}
          </td>
          <td>
            {{ result.timeUsed | clock }}
          </td>
          <td>
            {{ result.timeLimit | clock }}
          </td>
          <td>
            {{ result.createdAt | formatDateTime }}
          </td>
        </tr>
      </tbody>
    </table>
    <h3>
      Details:
    </h3>
    <card
      v-for="(item, index) in result.details"
      :data="item"
      :key="index"
      />
  </div>
</template>

<script>
import Card from './components/card.vue';

export default {
  components: {
    Card,
  },
  data() {
    return {
      result: {
        details: [],
      },
      loading: true,
    };
  },
  mounted() {
    const { id } = this.$route.params;
    this.$axios.get(`${this.$url.results}/${id}`)
      .then((resp) => {
        this.result = resp.data;
        this.loading = false;
      })
      .catch(() => {
      });
  },
};
</script>

<style lang="scss" scoped>

.result-container {
  padding: 0.8em 1em;
  color: #777;
}

</style>
